@import url('https://fonts.googleapis.com/css2?family=Lobster+Two:wght@700&family=Nunito:wght@600&display=swap');

:root {
    --border: #443152;
    --white: #FFF9EC;
    --yellow-shadow: #E5AF00;
    --yellow: #FEE289;
    --red: #EE4B50;
}

* {
    box-sizing: border-box;
}

html{
    height: 100%;
}

body {
    margin: 0;
    font-family: 'Nunito', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: var(--border);
    background: repeating-linear-gradient(to right,
            var(--red),
            var(--red) 60px,
            var(--white) 60px,
            var(--white) 110px);
    height: 100%;
    padding: 96px 48px;
}

#root{
    height: 100%;
}

.App{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    justify-content: space-between;
}

button {
    font-family: 'Lobster Two', cursive;
    border: 3px solid var(--border);
    font-size: 4rem;
    padding: 32px 80px;
    border-radius: 5px;
    background: linear-gradient(180deg, #285797, #285797 16px, #244F85 16px, #244F85 126px, #224676 126px);
    color: var(--white);
    text-shadow: var(--border) 0px 3px 0px;
    cursor: pointer;
}

